import React from 'react';
import profile from './img/profilecirc.png';

class Navbar extends React.Component {
    render() {
        const imgstyle = {
            width: '60px',
            height: 'auto',
            padding: '4px',
        }
        const navstyle = {
            backgroundColor: '#6d0016',
            borderBottom: '5px solid white',
            position: 'fixed',
            top: '0',
            width: '100%',
            zIndex: '2',
        }
        const toptext = {
            color: '#a29c9a',
            textWeight: "bold",
            textAlign: 'center',
            margin: 'auto auto',
            padding: '5px 0 0 0',
            textShadow: '2px 2px black',
        }

        return (
            <div style={navstyle}>

                <div className='c'>
                    <div className='col-2 col-s5'>
                        <img className='lg-float-right' src={profile} alt="" style={imgstyle} />
                    </div>
                     <div className='col-8 col-s7'>
                        <h1 className='sm-font-h1 xsm-font-h1 lg-text' style={toptext}> Kancelaria  Radcy Prawnego Krystian Szulc</h1>
                    </div>
                    
                </div>
            </div>
            )
            }
            }

export default Navbar;