import React from 'react';
import court from './img/court.png'

class Hist extends React.Component {
    render() {
        return (
            <div className='page'>
                <div className='col-8 col-xs12' style={{ display: 'none' }}>
                    <h1 id='histh1'> Historia</h1>
                </div>
                <div className='col-8 col-xs12'>
                    <p>Jestem absolwentem Uniwersytetu Śląskiego ( dyplom 1982 ). W latach 1985-1988 aplikacja radcowska, w latach 1998-1992 pracowałem w obsłudze prawnej przedsiębiorstw.</p>
                    <p>Kancelaria radcy prawnego powstała w 1992 roku w Katowicach.</p>
                    <p>Od początku działalności kancelaria specjalizowała się w obsłudze podmiotów gospodarczych, w tym reprezentowaniu przed sądami i organami administracji państwowej.</p>
                    <p>W 1995 roku przebywałem na stażu w Berlinie w Kancelarii Hengeller Muller, gdzie zapoznałem się z niemieckim system prawa cywilnego i gospodarczego.</p>
                    <p>W roku 2004 Kancelaria rozpoczęła współpracę ze <strong>Śląską Izbą Aptekarską</strong> w Katowicach w zakresie obsługi prawnej.</p>
                    <p>Od tego czasu kancelaria specjalizuję się w sprawach związanych z prawem farmaceutycznym oraz rynkiem aptek.</p>
                    <p>W roku 2016/2017 brałem aktywny udział w opracowaniu propozycji do projektu zmian prawa farmaceutycznego i wprowadzenia apteki dla aptekarza.</p>
                    <p>Prowadzę szkolenia dla farmaceutów oraz publikuję w sprawach z zakresu prawa farmaceutycznego i spraw związanych z aptekami.</p>
                    <p>Kancelaria kształci aplikantów.</p>
                    <p>Kancelaria posiada polisę generalną PZU zgodnie z ustawą o radcach prawnych.</p>
                </div>
                <div className='col-4 col-xs12'>
                    <img src={court} alt='' />
                </div>
            </div>
            )
    }
}

export default Hist